import produce from "immer";
import { cloneDeep } from "lodash";
import {
  addObjectByObjectType,
  triggerDeleteObjectIds,
  updateObjectPosition,
} from "../../threejs/eventEmitterHandlers";
import { ChangeObjectsProperty, AppEventType } from "../AppEventType";
import { ChangeObjectsPropertyHistory } from "../AppHistory";
import { refreshReactState } from "../refreshReactState";
import { setStore } from "../store";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import dayjs from "dayjs";

export class ChangeObjectsPropertyHandler extends StoreObjectHandler<ChangeObjectsProperty> {
  run() {
    const { change } = this.event.payload;
    const historyChange: ChangeObjectsProperty["payload"]["change"] = [];
    let newStore = produce(this.store, (draft) => {
      change.forEach((obj) => {
        if (draft.objects[obj.id] === undefined) {
          console.warn("no object found in store", obj.id);
          return;
        }
        const keyNames = Object.keys(obj.property);
        const oneChange = {
          id: obj.id,
          property: {},
        };
        keyNames.forEach((key) => {
          oneChange["property"][key] = cloneDeep(draft.objects[obj.id][key]);
        });
        historyChange.push(oneChange);
        keyNames.forEach((key) => {
          draft.objects[obj.id][key] = obj.property[key];
        });
      });
    });
    // setStore(newStore);
    if (this.saveToHistory) {
      const historyEvent: ChangeObjectsPropertyHistory = {
        do: this.event,
        redo: this.event,
        undo: {
          name: AppEventType.ChangeObjectsProperty,
          payload: { change: historyChange },
        },
        createdAt: dayjs().toISOString()
      };
      this.history.push(historyEvent);
    }
    
    newStore = this.getStoreWithHistory(newStore);

    change.forEach((obj) => {
      if (newStore.objects[obj.id] === undefined) {
        console.warn("no object found in store", obj.id);
        return;
      }

      const keyNames = Object.keys(obj.property);
      const isPositionOnly =
        keyNames.length === 1 && keyNames.includes("position") === true;
      if (isPositionOnly) {
        const object = newStore.objects[obj.id];
        updateObjectPosition(
          obj.id,
          object.objectType,
          object.position.x,
          object.position.y
        );
      } else {
        triggerDeleteObjectIds([
          { id: obj.id, objectType: newStore.objects[obj.id].objectType },
        ]);

        addObjectByObjectType(newStore.objects[obj.id]);
      }
    });

    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);

    // updateObjectsProperty(this.event);

    // refreshReactState(newStore);
  }
}
