const IconActionsTypeEditBeadColor: React.FC<{}> = (props) => {
  const backgroundColors = useSelector((state: Editor) => {
    const colors: string[] = [];
    state.selectedIds.forEach((id) => {
      if (!state.objects[id]) {
        return;
      }
      const color = (state.objects[id] as Bead).color;
      if (colors.includes(color) === false) {
        colors.push(color);
      }
    });
    return colors;
  });

  if (backgroundColors.length === 0) {
    return <React.Fragment />;
  }

  const isMultipleColors = backgroundColors.length > 0;
  // const defaulColors = ["orange" , "yellow", "green", "cyan", "blue", "violet"]
  const colors = backgroundColors.slice(0, 6);
  const rainbowBg = `linear-gradient(to right, ${colors.join()})`;
  // const backgroundColor = isMultipleColors
  //   ? rainbowBg
  //   : backgroundColors[0].toString();

  console.log("jakie kolory?", isMultipleColors, backgroundColors, rainbowBg);
  return (
    <div className={"icon-action-type-color"}>
      <div
        // @TRANSLATION
        data-tip={"More"}
        data-place={"bottom"}
        data-type={"dark"}
        data-effect={"solid"}
      >
        <div
          className={"value"}
          style={{
            backgroundColor: backgroundColors[0],
            background: isMultipleColors ? rainbowBg : undefined,
          }}
        >
          &nbsp;
        </div>
      </div>
    </div>
  );
};

